@import 'styles/variables';

.events {
	padding-top: 0px;
	display: flex;
	flex-direction: column;

	&__title {
		padding-bottom: 8px;
		font-family: $font-roboto-bold;
		font-size: 24px;
		letter-spacing: 0.96px;
		background: white;
		color: $color-black-dark;
		position: sticky;
		top: 101px;
	}

	&__btn {
		// width: 260px;
		margin-bottom: 16px;
		display: flex;
		justify-content: flex-end;
		background: white;
		position: sticky;
		top: 133px;
		z-index: 10;

		&_link {
			padding: 24px 52px;
			display: inline-block;
			font-family: $font-roboto-bold;
			font-size: 18px;
			color: $color-white;
			border-radius: 16px;
			background: $color-blue-btn;
			box-shadow: 4px 4px 6px 0px rgba(60, 61, 64, 0.25); 
		}
	}

	&__list {
		padding-top: 40px;
		display: flex;
		flex-direction: column;
		gap: 54px;
	}

	&__item {
			
		&_title {
			font-family: $font-roboto-bold;
			font-size: 20px;
			letter-spacing: 0.6px;
			color: $color-black-dark;
		}

		&_text {
			font-family: $font-mulish-light;
			font-size: 16px;
			color: $color-black-dark;

			&_red {
				color: $color-red;
			}
		}
	}
}

.event {

	&_back {
		margin-bottom: 12px;
		display: flex;
		align-items: center;
		font-family: $font-mulish-bold;
		font-size: 14px;
		color: #000;

		&::before {
			content: url('../../assets/svg/ArrowToBack.svg');
			width: 24px;
			height: 24px;
			margin-right: 8px;
		}
	}

	&__title {
		margin-bottom: 75px;
		font-family: $font-roboto-bold;
		font-size: 24px;
		letter-spacing: 0.96px;
		color: $color-black-dark;
	}

	&__form {
		display: flex;
		flex-direction: column;

		&_label {
			margin-bottom: 12px;
			font-family: $font-mulish-light;
			font-size: 20px;
			color: $color-black-dark;
		}

		&_input {
			width: 408px;
			padding: 12px 16px;
			margin-bottom: 60px;
			font-family: $font-mulish-light;
			font-size: 18px;
			border-radius: 10px;
			border: 1px solid #DADBDB;
			background: #F2F6F8;
		}

		&_btn {
			margin-bottom: 40px;

			&_link {
				width: 260px;
				padding: 20px 60px;
				display: inline-block;
				font-family: $font-roboto-bold;
				font-size: 18px;
				color: $color-white;
				border-radius: 16px;
				background: $color-blue-btn;
				box-shadow: 4px 4px 6px 0px rgba(60, 61, 64, 0.25); 
			}
		}
	}
}
