@import 'styles/variables';

.sidebar {
	height: 100%;
	padding: 40px 0px 40px 24px;
	flex: 0 0 200px;
	display: flex;
	flex-direction: column;
	align-items: center;
	font-family: $font-mulish-light;
	font-size: 12px;
	border-radius: 20px;
	background: linear-gradient(180deg, #778395 0%, #555F70 100%);
	box-shadow: 4px 0px 10px 0px rgba(56, 55, 58, 0.30);
	color: $color-white;
	overflow-y: auto;

	&__logo {
		margin-bottom: 12px;
	}

	&__user {
		padding: 0px 64px 40px 20px;
		align-self: flex-start;
		display: flex;
		align-items: center;
		font-family: $font-mulish-light;
		font-size: 14px;
		color: $color-white;

		&::before {
			content: url('../../assets/svg/Ellipse.svg');
			display: block;
			width: 28px;
			height: 28px;
			margin-right: 16px;
		}
	}

	&__navbar {
		width: 100%;
		padding-left: 20px;
		flex: 1 1 auto;
	}

	&__footer {
		padding-left: 50px;
		align-self: flex-start;

		:hover {
			color: #0093F0;
			cursor: pointer;
		}

		&_text {
			font-family: $font-mulish-regular;
			font-size: 14px;
			color: $color-white;
		}
	}
}